import { Component, Prop, h, Host } from '@stencil/core';
import { cva } from 'class-variance-authority';

const variants = cva(
    'group relative flex w-full cursor-pointer items-center gap-8 overflow-visible rounded-md p-8 text-left text-button transition-[background-color,color,box-shadow] duration-150 ease-in-out focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-active-foreground/30',
    {
        variants: {
            active: {
                true: 'bg-sidebar-primary text-sidebar-primary-foreground',
                false: 'text-sidebar-foreground',
            },
        },
    },
);

const focusIndicatorVariants = cva(
    'pointer-events-none absolute left-0 top-0 z-0 h-full w-full rounded-md transition-colors duration-300 ease-in-out',
    {
        variants: {
            active: {
                true: 'group-hover:bg-blue-900/30 group-active:bg-blue-900/70',
                false: 'group-hover:bg-surface-4/10 group-active:bg-surface-4/20',
            },
        },
    },
);

/**
 * @category Navigation
 * @description A sidebar menu item component for the sidebar.
 */

@Component({
    tag: 'atui-sidebar-menuitem',
    shadow: false,
})
export class AtuiSidebarMenuitemComponent {
    /**
     * Label to be displayed for the menu item
     */
    @Prop() label?: string;

    /**
     * Icon to be displayed with the label
     */
    @Prop() icon?: string;

    /**
     * Alert badge for the menu item
     */
    @Prop() badge?: string;

    /**
     * Will change the styling of the menuitem when set
     */
    @Prop() is_active?: boolean;

    render() {
        const classname = variants({
            active: this.is_active,
        });
        const focusIndicatorClassname = focusIndicatorVariants({
            active: this.is_active,
        });

        return (
            <Host
                role="menuitem"
                class={classname}
            >
                {this.badge && (
                    <div
                        data-name="menu-badge"
                        role="presentation"
                        class="absolute left-[18px] top-[-4px] z-10"
                    >
                        <atui-badge
                            class="!rounded-[4px] !px-[3px] !py-4 !text-[11px] !font-medium !leading-[10px]"
                            type="error"
                            impact="high"
                            rounded={false}
                            label={this.badge}
                        ></atui-badge>
                    </div>
                )}
                {this.icon && (
                    <span class="material-icons z-10 flex items-center justify-center text-icon-xl">
                        {this.icon}
                    </span>
                )}
                {this.label && (
                    <span
                        data-name="sidebar-menu-item-label"
                        class="z-10 truncate whitespace-nowrap group-data-[state=collapsed]/sidebar-wrapper:hidden"
                    >
                        {this.label}
                    </span>
                )}
                <slot />
                <div
                    data-name="focus-indicator"
                    role="presentation"
                    class={focusIndicatorClassname}
                ></div>
            </Host>
        );
    }
}
